export default {
	data() {
		return {
			// 临时图片路径
			imgSrc: '' ,
			// 用户信息
			userInfo: {},
			// 最新图片
			faceImage: ''
		}
	},
	onShow() {
		this.userInfo = this.getUserInfo("userToken")
	},
	onLoad(params) {
		console.log(params)
		this.imgSrc = params.imgSrc
		
	},
	methods: {
		//  重新选择
		againImg() {
			uni.chooseImage({
				count: 1,  // 限制9张
				sizeType: ["compressed"],  //  压缩图 可以指定是原图还是压缩图，默认二者都有
				sourceType: ["album"],  //  从相册选择
				success: (data) => {
					// 获取图片临时路径
					this.imgSrc = data.tempFilePaths[0]
					uni.getStorageSync("newImg",this.imgSrc)
				}
			})
		},
		
		// 确定上传
		upload() {
			uni.showLoading({
				title: "上传中,请稍后",
				mask: true
			})
			
			uni.uploadFile({
				url: `${this.$api}/user/uploadFace?userId=${this.userInfo.id}&qq=122212489`,
				filePath: this.imgSrc,
				name: "file",
				header: {
					"headerUserId": this.userInfo.id,
					"headerUserToken":  this.userInfo.userUniqueToken
				},
				success: (res) => {
					const user = JSON.parse(res.data)
					console.log(user)
					// if (user.status == 200) {
						const userInfo = user.data;
						uni.setStorageSync("userToken", userInfo);
						uni.navigateBack({
							delta: 1
						})
					// } else {
					// 	uni.showToast({
					// 		title: res.data.msg,
					// 		image: "../../static/icos/error.png",
					// 		duration: 2000
					// 	})
					// }
				},
				complete() {
					uni.hideLoading()
				}
			})
		}
	}
}
